
.com-line canvas {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.com-line .text {
  /*chrome浏览器不会显示小于12px的文本,所以用缩放*/
  position: absolute;
  font-size: 12px;
  text-align: center;
  bottom: -20px;
  height: 20px;
  line-height: 20px;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  opacity: 1;
  -webkit-transition: all 1s 1.5s;
  transition: all 1s 1.5s;
}
.com-line .text.show {
  opacity: 1;
}



