
.com-pie canvas {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.com-pie .text {
  position: absolute;
  text-align: center;
  z-index: 99;
  height: 24px;
  line-height: 13px;
  opacity: 0;
  -webkit-transition: all 1s linear;
  transition: all 1s linear;
}
.com-pie .text.show {
  opacity: 1;
}



